<template>
	<div class="hello">
		<div v-html="loginBlock"></div>
	</div>
</template>

<script>
	export default {
		name: 'LoginPage',
		mounted() {
			this.login()
		},
		data () {
			return {
				loginBlock: ''
			}
		},
		methods: {
			login() {
				this.$api.get('https://github.com/login/oauth/authorize', {
					redirect_uri: 'http://localhost:8080/#/my_repo',
					scope: []
				}).then((response) => {
					this.loginBlock = response
				})
			}
		}
	}
</script>

<style scoped>
	h1, h2 {
		font-weight: normal;
	}

	ul {
		list-style-type: none;
		padding: 0;
	}

	li {
		display: inline-block;
		margin: 0 10px;
	}

	a {
		color: #42b983;
	}
</style>



