import Vue from 'vue'
import Http from 'vue-resource'

Vue.use(Http)

Vue.http.headers['Content-Type'] = 'application/json';

Vue.http.options.root = 'https://api.github.com/';

let Api = {

	_request(method, url, data) {

		switch (method) {
			case "get":
				data = {"params": data}
				break;
			case "delete":
				data = {"body": data}
				break;
		}

		return Vue.http[method](url, data).then((response) => {
			return response.body
		}).catch(this.checkError)
	},

	get(url, data) {
		return this._request('get', url, data)
	},
	post(url, data) {
		return this._request('post', url, data)
	},
	put(url, data) {
		return this._request('put', url, data)
	},
	patch(url, data) {
		return this._request('patch', url, data)
	},
	remove(url, data) {
		return this._request('delete', url, data)
	},

	checkError(error) {
		console.info(error.status)
		switch (error.status) {
			case 422:
				let errors = [], fieldArr, i, j, result

				if(typeof error.body.data.errors === 'string') {
					Vue.event.toast('e', error.body.data.errors)
					return false
				}

				for (i in error.body.data.errors) {
					fieldArr = error.body.data.errors[i]

					if(typeof fieldArr === 'string')
						errors.push(fieldArr)
					else {
						for (j in fieldArr)
							errors.push(fieldArr[j])
					}
				}

				result = errors.join('\n')
				Vue.event.toast('e', result)

				break;
			case 403:
				Vue.event.toast('e', 'Вы не авторизованы', true)
				break;
			case 0:
				return false
				break;
			default:
				Vue.event.toast('e', (error.data.error) ? error.data.error : error.data.errors)
				break;
		}
	}
}

Api.install = function(Vue) {
	Vue.prototype.$api = Api
	Vue.api = Api
}

export default Api



